<script>
import moment from 'moment'
import { foods } from  '@/constants/foods.js'

export default {
  props: {
    orders: {
      type: Array,
    },
    language: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      active: 0,
      type: 'dine in',
      date: null,
      time: null,
      name: '',
      numOfPpl: 0,
      languagePreference: 'chinese',

      showLink: false,
    }
  },
  methods: {
    handleConfirm() {
      if (this.active == 1) {
        if (this.haveInvalidField()){
          this.$message.error(this.isEnglish ? 'Oops, Please key in all field.' : 'ËØ∑Â°´ÂÜôÊâÄÊúâËµÑÊñô' )
          this.showLink = false
        } else {
          this.showLink = true
        }
      }
    },
    haveInvalidField() {
      if (this.date == null) return true
      if (this.time == null) return true
      if (this.name == null || this.name == '') return true
      if (this.numOfPpl == 0) return true
      return false
    },
    handleStep(direction) {
      if (direction == 'back') {
        this.$emit('showMenu', true)
        this.active--
      }
      if (direction == 'next') {
        // if (this.orders.length == 0) {
          // this.$confirm(
          //   this.isEnglish ? 'No food was added. You want to proceed to next step?' 
          //     : 'ÊÇ®ËøòÊú™Ê∑ªÂä†‰ªª‰ΩïÈ£üÁâ©ÔºåÁ°ÆÂÆö‰∏ã‰∏ÄÊ≠•Ôºü'
          // , 'Warning', {
          //   confirmButtonText: 'OK',
          //   cancelButtonText: 'Cancel',
          //   type: 'warning',
          //   center: true,
          // }).then(() => {
            this.$emit('showMenu', false)
            this.active++
          // });
        // }
      }
    },
    getLink() {
      return `https://wa.me/60126549696?text=${this.getWhatsappMsg()}`
    },
    getWhatsappMsg() {
      const nextLine="%0A"
      let result = `‰Ω†Â•Ω Madam Tan, ÊàëÊòØ${this.name}. ËøôÊòØ‰ªéÁΩëÁ´ôorderÔºö`
      result += nextLine
      result += `ËØ≠Ë®Ä: ${this.languagePreference}`
      result += nextLine
      result += `ÁßçÁ±ª: ${this.type}`
      result += nextLine
      result += `Êó∂Èó¥: ${moment(this.date).format('YYYY-MMM-DD (ddd)')} ${moment(this.time, 'HH:mm').format('hh:mma')}`
      result += nextLine
      result += `‰∫∫Êï∞: ${this.numOfPpl}`
      result += nextLine
      result += `----- ËèúÂçï -----`
      result += nextLine
      this.orders.forEach(order => {
        let found = foods.find(el => el.id == order.id)
        result += `${order.amount} -> ${found.name.chinese}`
        result += nextLine
      })
      console.log(result, this.time)
      return result
    }
  },
  computed: {
