  .description {
    margin: 0 0.5rem;
  }

  .el-divider {
    &.horizontal {
      margin: 0.3rem auto;
      width: 9rem;
    }
    &.vertical {
      margin: auto 0.3rem;
      height: 9rem;
      }
  }
  
  .food-info {
    margin: auto 0;
  }

  .amount {
    margin-top: 2rem;
